import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs';
import { Router } from '@angular/router';

@Component({
  selector: 'app-wizard-cards',
  templateUrl: './wizard-cards.component.html',
  styleUrls: ['./wizard-cards.component.css']
})
export class WizardCardsComponent implements OnInit {

  constructor(private modalService: NgbModal,private http: HttpClient,private _router: Router) { 
    this.getJSON().subscribe(responseJson => {

      if (responseJson && responseJson.status) {
        this.processList=responseJson.message.data
        
        }
    });
  }
  public processList: any = [];
  currentStep = 0;
  public popupModal;
  settings = {
    showHrLine: true // Horizontal Line
  };
  public data = [
    {
      label: 'Step 1',
      stepnumber : '1'
    },
    {
      label: 'Step 2',
      stepnumber : '2'
    },
    {
      label: 'Step 3',
      stepnumber : '3'
    },
    {
      label: 'Step 4',
      stepnumber : '4'
    }
  ]
  @ViewChild('parameterWizard') parameterWizard;
  public modal_options: NgbModalOptions = {
    size: 'lg',
    backdrop: 'static'
  };
  private _jsonURL = 'assets/jsons/wizardList.json';
  ngOnInit() {
  }
  onclick(step,i) {
    try {
      this.currentStep = i+1;
      if (step['stepnumber']) {
        this.stepUpdate(step['stepnumber']);
      }
      // this.step.emit(step);
    } catch (error) {
      console.error(error);
    }
  }
  stepUpdate(number) {
    try {
      this.data.forEach((eachStep) => {
        if (eachStep['stepnumber'] === number) {
          eachStep['active'] = true;
        } else {
          eachStep['active'] = false;
        }
      });
    } catch (error) {
      console.error(error);
    }
  }
  openWizard(id){
    // this.popupModal = this.modalService.open(this.parameterWizard,this.modal_options);
    let url = `/wizards/wizard/${id}`
    this._router.navigate([url]);
  }
  public getJSON(): Observable<any> {
    return this.http.get(this._jsonURL);
  }

}
