import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
// import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-wizard',
  templateUrl: './wizard.component.html',
  styleUrls: ['./wizard.component.css']
})
export class WizardComponent implements OnInit {
  public cardId;
  public currentActiveStep;
  public data;
  public stepData;
  stepForm;
  files: File[] = [];
  public currentStep = "";
  @ViewChild('selectFile') Select_File;
  userUploadFile: File;
  settings = {
    showHrLine: true // Horizontal Line
  };
  submitted = false;
  constructor(private http: HttpClient,
    private route: ActivatedRoute,
    private _router: Router,
  ) {
    this.getJSON().subscribe(responseJson => {
      if (responseJson && responseJson.status) {
        this.data = responseJson.message.steps[this.cardId];
      }
      if (this.data[0]) {
        this.data[0]['active'] = true;
        this.currentActiveStep = 0;
        this.getStepData(this.data[0]['key']);
      }
    });
  }
  private _jsonURL = 'assets/jsons/wizardSteps.json';
  public getJSON(): Observable<any> {
    return this.http.get(this._jsonURL);
  }

  get f() { return this.stepForm.controls; }

  ngOnInit() {
    this.route.queryParams.subscribe(params => {
      this.cardId = this.route.snapshot.params['id']
    });
  }

  onclick(step, i) {
    try {
      if (i < this.currentActiveStep) {
        let stepId = this.data[i] ? this.data[i]['key'] : "";
        if(stepId){
          let url = `/wizards/wizard/${stepId}`
          this._router.navigate([url]);
        }
        this.currentActiveStep = i;
        this.currentStep = step.key;
        if (step['key']) {
          this.stepUpdate(step.key);
        }
        this.getStepData(step.key);
      } else {
        console.log("Please fill form");
      }
    } catch (error) {
      console.error(error);
    }
  }

  stepUpdate(key) {
    try {
      this.data.forEach((eachStep) => {
        if (eachStep['key'] === key) {
          eachStep['active'] = true;
        }
      });
    } catch (error) {
      console.error(error);
    }
  }

  getStepData(key) {
    this.stepForm = new FormGroup({});
    this.submitted = false;
    let url = 'assets/jsons/dynamicFormData.json';
    this.http.get(url).subscribe(responseJson => {
      if (responseJson && responseJson['status']) {
        this.stepData = responseJson['message'].data[key];
      }
      this.addFormControls(this.stepData.fields);
      this.getIcons();
    });
  }

  addFormControls(controls) {
    for (let field of controls) {
      let allValidators = [];
      const newFormControl = new FormControl();
      for (let val of field.validation) {
        if (val.rule === 'required') {
          allValidators.push(Validators.required);
        } else if (val === "pattern") {
          allValidators.push(Validators.pattern(field.validation_value[val]))
        }
      }
      newFormControl.setValidators(allValidators);
      this.stepForm.addControl(field.key, newFormControl)
    }

  }

  onSubmit() {
    this.submitted = true;
    if (this.stepForm.valid) {
      let nextStepId = this.data[this.currentActiveStep + 1] && this.data[this.currentActiveStep + 1]['key'] ? this.data[this.currentActiveStep + 1]['key'] : "";
      if (nextStepId.length) {
        let url = `/wizards/wizard/${nextStepId}`
        this._router.navigate([url]);
        this.navigateSteps(true);
      } else {
        console.log("Successfully created step under process area");
        let url = "/wizards/cards"
        this._router.navigate([url]);
      }
    }
    // console.log(this.stepForm)
  }

  backToPreviousStep() {
    let previousStepId = this.data[this.currentActiveStep - 1] && this.data[this.currentActiveStep - 1]['key'] ? this.data[this.currentActiveStep - 1]['key'] : "";
    if (previousStepId.length) {
      let url = `/wizards/wizard/${previousStepId}`
      this._router.navigate([url]);
      this.navigateSteps(false);
    }
  }

  navigateSteps(flag) {
    this.currentStep = flag ? this.data[this.currentActiveStep + 1]['key'] : this.data[this.currentActiveStep - 1]['key'];
    this.stepUpdate(this.currentStep);
    this.getStepData(this.currentStep);
    this.currentActiveStep = flag ? this.currentActiveStep + 1 : this.currentActiveStep - 1;
  }

  goBack() {
    let url = "/wizards/cards"
    this._router.navigate([url]);
  }

  onSelect(event) {
    console.log(event);
    this.files = [];
    if (event.addedFiles.length) {
      this.files.push(...event.addedFiles);
      this.stepForm.patchValue({ renameFile: this.files[0]['name'] })
    } else if (event.rejectedFiles.length) {
      console.log("Please select valid file")
    }

  }

  onRemove(event) {
    // console.log(event);
    this.stepForm.patchValue({ renameFile: "" })
    this.files.splice(this.files.indexOf(event), 1);
  }

  getIcons() {
    try {
      let url = "https://api.npms.io/v2/search?q=scope:angular"
      this.http.get(url).subscribe(response => {
        console.log(response);
      })
    } catch (error) {
      console.log("error", error)
    }
  }
}
